
'use client';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Users, Crown, ExternalLink, ChevronDown } from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';
import { motion } from 'framer-motion';
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import type { FC } from 'react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import React from 'react';

// Define Player interface for clarity within TeamType
interface Player {
  id: string;
  name: string;
}

export interface TeamType {
  id: string;
  name: string;
  owner: string;
  logo: string;
  players: Player[]; // Each player is an object
  totalGoals?: number;
  totalAssists?: number;
  matchesPlayed?: number;
  matchesWon?: number;
  matchesLost?: number;
  yellowCards?: number;
  redCards?: number;
}

// Data updated as per user request, ensuring players is Player[]
const teams: TeamType[] = [
  { id: 't1', name: 'Dongre Super Kicks', owner: 'Amol Panchangane', logo: '/images/teams/dsk.jpg', players: [{id: 'p1', name: 'Amey Kawle'}, {id: 'p2', name: 'Arjun Desai'}, {id: 'p3', name: 'Satej Dhaiphule'}, {id: 'p4', name: 'Shubham Parulkar'}, {id: 'p5', name: 'Vikram'}] },
  { id: 't2', name: 'Red Devils', owner: 'Jayesh | Gaurav', logo: '/images/teams/rd.jpg', players: [{id: 'p6', name: 'Harshvardhan Jadwani'}, {id: 'p7', name: 'Krish Mistry'}, {id: 'p8', name: 'Nirvaan Sood'}, {id: 'p9', name: 'Shlok Desai'}, {id: 'p10', name: 'Shreyas K'}] },
  { id: 't3', name: 'Shadow Hawks', owner: 'Abhijeet | Akhil', logo: '/images/teams/sh.jpg', players: [{id: 'p11', name: 'Aaron Dsouza'}, {id: 'p12', name: 'Arnav Chaudhary'}, {id: 'p13', name: 'Deep Patel'}, {id: 'p14', name: 'Harsh Daware'}, {id: 'p15', name: 'Tanish Gawade'}] },
  { id: 't4', name: 'White Knights FC', owner: 'Pushpinder | Prateek', logo: '/images/teams/wk.jpg', players: [{id: 'p16', name: 'Anish'}, {id: 'p17', name: 'Atharva Sawant'}, {id: 'p18', name: 'Dhrishit Seal'}, {id: 'p19', name: 'Paras Patil'}, {id: 'p20', name: 'Sarthak Sharma'}] },
  { id: 't5', name: 'Real Pawcelona', owner: 'Atharva | Jordan | Ansh', logo: '/images/teams/RP.jpg', players: [{id: 'p21', name: 'Aarya Kawle'}, {id: 'p22', name: 'Ayaan Ansaari'}, {id: 'p23', name: 'Ansh Bhardwaj'}, {id: 'p24', name: 'Hridant Sood'}, {id: 'p25', name: 'Parth Jadwani'}] },
];


const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
      delayChildren: 0.2,
    },
  },
};

const itemVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.5 } },
};

// This component will render the visual header part of the team card
const TeamCardHeaderDisplay: FC<{ team: TeamType }> = ({ team }) => (
  <div className="flex items-center gap-4 p-4 w-full"> {/* Ensure padding for visibility */}
    <Avatar className="h-12 w-12">
      <Image
        src={team.logo || '/images/placeholder-logo.jpg'} // Fallback src if team.logo is empty
        alt={`${team.name || 'Team'} Logo`}
        width={48}
        height={48}
        className="rounded-full object-cover"
        data-ai-hint="team logo sport crest"
        onError={(e) => {
          e.currentTarget.onerror = null; 
          e.currentTarget.src = `https://placehold.co/48x48.png`; // Default placeholder
        }}
      />
      <AvatarFallback>{team.name ? team.name.substring(0, 2).toUpperCase() : 'N/A'}</AvatarFallback>
    </Avatar>
    <div className="flex-1 min-w-0"> {/* Added min-w-0 for better flex handling of truncation */}
      <CardTitle className="text-xl group-hover:text-primary transition-colors truncate">
        {team.name || 'Unnamed Team'}
      </CardTitle>
      <p className="text-sm text-muted-foreground truncate">
        <Crown className="inline-block w-4 h-4 mr-1 text-amber-500 shrink-0" />
        {team.owner || 'Owner Unknown'}
      </p>
    </div>
  </div>
);

export default function TeamsPage() {
  return (
    <motion.div
      className="space-y-6"
      initial="hidden"
      animate="visible"
      variants={containerVariants}
    >
      <motion.h1
        className="text-3xl font-bold flex items-center gap-2"
        variants={itemVariants}
      >
        <Users className="w-8 h-8 text-primary" />
        Teams
      </motion.h1>
      <Accordion type="single" collapsible className="w-full grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {teams.map((team) => (
          <motion.div key={team.id} variants={itemVariants}>
            <Card className="overflow-hidden rounded-lg shadow-md hover:shadow-xl transition-shadow duration-300 flex flex-col">
              <AccordionItem value={team.id} className="border-b-0 w-full">
                <AccordionTrigger 
                  asChild
                  className={cn(
                    "hover:no-underline focus:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 rounded-t-lg w-full group",
                    "data-[state=open]:bg-muted/10 data-[state=closed]:bg-transparent"
                  )}
                >
                  {/* The direct child of AccordionTrigger when asChild is true becomes the trigger */}
                  <div className="flex items-center justify-between w-full relative">
                    <TeamCardHeaderDisplay team={team} />
                    {/* Button for navigation, separate from accordion toggle logic */}
                    <Button
                        asChild
                        variant="ghost"
                        size="icon"
                        className="text-muted-foreground hover:text-primary hover:bg-primary/10 z-10 mx-1 flex-shrink-0" 
                        onClick={(e) => {
                            e.stopPropagation(); // Prevent accordion toggle when clicking this link
                        }}
                        aria-label={`View details for ${team.name}`}
                    >
                        <Link href={`/teams/${team.id}`}>
                            <ExternalLink className="w-5 h-5" />
                        </Link>
                    </Button>
                    {/* Chevron is part of the visual trigger, but its click is handled by AccordionTrigger */}
                    <ChevronDown
                      className="h-5 w-5 text-muted-foreground transition-transform duration-200 group-data-[state=open]:rotate-180 mr-3 flex-shrink-0"
                    />
                  </div>
                </AccordionTrigger>
                <AccordionContent>
                  <div className="p-4 border-t bg-muted/5 dark:bg-muted/10">
                    <h4 className="font-semibold mb-2 text-card-foreground text-sm">Team Roster ({team.players.length} Players):</h4>
                    <ul className="list-disc list-inside space-y-1 text-sm text-muted-foreground">
                      {team.players.map((player) => (
                        <li key={player.id}>{player.name}</li>
                      ))}
                      {team.players.length === 0 && <li>No players listed for this team.</li>}
                    </ul>
                  </div>
                </AccordionContent>
              </AccordionItem>
            </Card>
          </motion.div>
        ))}
      </Accordion>
    </motion.div>
  );
}

export const getTeamsData = () => teams;
